import React from "react";
import { ContentToggler } from "../../Styles/HeaderStyles";
import { icons } from "../Icons";

function contentToggle() {
  var brand = document.getElementById("brand-link");
  var content = document.getElementById("content-list");
  // var themeButton = document.getElementById("theme-toggler");
  if (brand.style.display === "none") {
    brand.style.display = "flex";
    content.style.display = "none";
  } else {
    brand.style.display = "none";
    content.style.display = "flex";
  }
}

function ToggleContentButton() {
  return <ContentToggler onClick={contentToggle}>{icons.bars}</ContentToggler>;
}

export default ToggleContentButton;
