import React from "react";
import { contentModules } from "../Content/Content_Modules";
import { PageContainer } from "../Styles/BodyStyles";

function Products() {
  var list = [];
  for (var i = 0; i < contentModules.length; i++) {
    if (contentModules[i].type === "products")
      list.push(contentModules[i].content);
  }
  return <PageContainer>{list}</PageContainer>;
}

export default Products;
