import React from "react";
import { icons } from "../Components/Icons";
import { contentModules } from "../Content/Content_Modules";
import { PageContainer, BodyLogo } from "../Styles/BodyStyles";

function Home() {
  var recent10 = [];
  for (var i = 0; i < contentModules.length; i++) {
    //i<10
    recent10.push(contentModules[i].content);
  }
  return <PageContainer>
    <BodyLogo>{icons.brandLogo}</BodyLogo>
      {recent10}</PageContainer>;
}

export default Home;
