import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Products from "./Pages/Products";
import styled, { ThemeProvider } from "styled-components";
import { lightTheme } from "./Styles/Themes";
import { ButtonBox, HeaderContainer } from "./Styles/HeaderStyles";
import { BodyContainer } from "./Styles/BodyStyles";
import Photos from "./Pages/Photos";
import About from "./Pages/About";
import StockMarket from "./Pages/Stock_Market";
import Home from "./Pages/Home";
import GlobalStyle from "./Styles/GlobalStyles";
import { FooterContainer } from "./Styles/FooterStyles";
import Footer from "./Components/Footer/Footer";
import ToggleContentButton from "./Components/Header/Toggle_Content_Button";
import NavList from "./Components/Header/Nav_List";

const HTML = styled.div`
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
`;
function App() {
  // Theme Setting & theme changing Function

  //Theming ends

  return (
    <ThemeProvider theme={lightTheme}>
      <>
        <GlobalStyle />
        <BrowserRouter>
          <HTML>
            <HeaderContainer>
              <ButtonBox>
                <ToggleContentButton />
              </ButtonBox>
              <NavList />
            </HeaderContainer>
            <BodyContainer>
              <Switch>
                <Route exact path="/blogs/">
                  <Home />
                </Route>
                <Route exact path="/blogs/stockmarket">
                  <StockMarket />
                </Route>
                <Route exact path="/blogs/photos">
                  <Photos />
                </Route>
                <Route exact path="/blogs/products">
                  <Products />
                </Route>
                <Route exact path="/blogs/about">
                  <About />
                </Route>
              </Switch>
            </BodyContainer>
            <FooterContainer>
              <Footer />
            </FooterContainer>
          </HTML>
        </BrowserRouter>
      </>
    </ThemeProvider>
  );
}

export default App;
// theme should get saved and then loaded using if to check if there was any intial theme
